.header-color {
  background: #00c9ff; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to left,
    #92fe9d,
    #00c9ff
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to left,
    #92fe9d,
    #00c9ff
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
  background: #00c9ff; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to left,
    #92fe9d,
    #00c9ff
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to left,
    #92fe9d,
    #00c9ff
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
}

.avatar-img {
  height: 300px;
  padding-top: 5em;
}
.banner-text {
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
  background: #251c36;
}

.banner-text h1 {
  font-size: 50px;
  font-weight: bold;
  color: white;
}
.banner-text h3 {
  color: rgb(222, 236, 236);
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i {
  color: rgb(19, 18, 18);
  font-size: 5em;
  padding: 8px;
}

.projects-grid {
  display: flex;
  flex-wrap: wrap;
}

/* Resume Page */

.resume-right-col {
  background: #27221f;
  color: white;
  padding: 2em;
}

.left-container {
  padding: 2em;
  display: flex;
  flex-direction: column;
  align-content: center;
}

a {
  text-decoration: none;
}
